import React from 'react';


import {  HashRouter, Switch, Route, Link } from 'react-router-dom';
import { StyleSheet, css } from 'aphrodite/no-important';
import AppStyle from './css/AppStyle';

const Header = (props) => {
    return(<header>


        <nav>
            <ul>
                <li id="page_home" className={css(AppStyle.menu_inline)}><Link to='/'></Link></li>
                <li id="page_home" className={css(AppStyle.menu_inline)}><Link to='/'></Link></li>
                <li id="page_production" className={css(AppStyle.menu_inline)}><Link to='/production'></Link></li>
                <li id="page_home" className={css(AppStyle.menu_inline)}><Link to='/'></Link></li>
                <li id="page_home" className={css(AppStyle.menu_inline)}><Link to='/'></Link></li>
                <li id="page_about" className={css(AppStyle.menu_inline)}><Link to='/about'>&nbsp;</Link></li>
                <li id="page_shopping_art" className={css(AppStyle.menu_inline)} ><Link to='/shopping_cart'></Link></li>
            </ul>
        </nav>
    </header>
)}
export default Header;


